@import "@/assets/styles/variables.scss";


.Block {
    position: relative;
    z-index: 2;
    margin-bottom: 120px;

    @media (max-width: $media-tablet) {
        margin-bottom: 90px;
    }

    @media (min-width: $media-tablet-landscape) {
        //&::before {
        //    content: '';
        //    position: absolute;
        //    right: -40%;
        //    top: -70%;
        //    width: 800px;
        //    height: 800px;
        //    border-radius: 899px;
        //    filter: blur(300px);
        //    @-moz-document url-prefix() {
        //        opacity: 0.1;
        //    }
        //    background: #2D2D2D;
        //    transform: translate3d(0, 0, 0);
        //    z-index: 1;
        //}


        &::after {
            content: '';
            position: absolute;
            left: -30%;
            top: -50%;
            width: 1119px;
            height: 899px;
            border-radius: 1119px;
            background: #070707;
            transform: translate3d(0, 0, 0);
            filter: blur(110px);
            @-moz-document url-prefix() {
                opacity: 0.1;
            }
            z-index: 0;
        }
    }
}

.Box {
    width: 100%;
    padding: 24px;
    margin: 0 auto;
    background-color: #000;
    border-radius: 36px;
    border: 1px solid #464646;
    position: relative;
    overflow: hidden;
    z-index: 2;

    @media (min-width: $media-tablet) {
        padding: 48px;
    }

    &::before {
        position: absolute;
        content: '';
        top: -25%;
        right: -60%;
        border-radius: 666px;
        opacity: 0.6;
        background: #5841DF;
        -webkit-filter: blur(130.50294494628906px);
        transform: translate3d(0, 0, 0);
        z-index: 10;
        -moz-filter: blur(130.50294494628906px);
        -ms-filter: blur(130.50294494628906px);
        filter: blur(130.50294494628906px);
        width: 666px;
        height: 400px;
        z-index: 0;
        @-moz-document url-prefix() {
            opacity: 0.1;
        }


        @media (min-width: 500px) {
            bottom: -90%;
            right: -40%;
        }


        @media (min-width: $media-tablet) {
            top: auto;
            bottom: -140%;
            right: -40%;
            border-radius: 1531px;
            opacity: 0.6;
            background: #5841DF;
            width: 1531px;
            height: 1531px;
            filter: blur(300px);
            -webkit-filter: blur(300px);
            -moz-filter: blur(300px);

            -ms-filter: blur(300px);
        }

        @media (min-width: $media-tablet-landscape) {
            bottom: -1200px;
            right: auto;
            left: -400px;
        }
    }
}

.WrapperTop {
