    //margin-bottom: 24px;
    position: relative;
    z-index: 2;

    @media (min-width: $media-tablet) {
        //margin-bottom: 36px;
    }

    @media (min-width: $media-tablet-landscape) {
        flex-direction: row;
    }
}

.Left {
