.Title {
    font-family: $font-base;
    font-weight: normal;
    //font-size: 40px;
    //line-height: 41px;
    font-size: 24px;
    line-height: 28px;
    position: relative;
    z-index: 2;

    //background-image: conic-gradient(from 267deg at 6.96% 50%, #FFF 0deg, rgba(255, 255, 255, 0.90) 36.81592583656311deg, rgba(255, 255, 255, 0.00) 360deg);
    //background-image:  conic-gradient(from 255deg at 0.92% 38.56%, #FFF 0deg, rgba(255, 255, 255, 0.90) 36.81592583656311deg, rgba(255, 255, 255, 0.00) 360deg);
    //background-image: conic-gradient(from 255deg at -1.08% 38.56%, #FFF 0deg, rgba(255, 255, 255, 0.9) 36.8159258366deg, rgba(255, 255, 255, 0) 360deg);
    background: linear-gradient(147deg, #FFF -10.51%, rgba(255, 255, 255, 0.00) 159.69%);
    color: transparent;
    background-size: 100%;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-position: top;
    background-repeat: no-repeat;

    @media (min-width: $media-tablet) {
        font-size: 40px;
        line-height: 48px;
        max-width: 470px;
    }

    @media (min-width: $media-tablet-landscape) {
        //max-width: 700px;
    }

    span {
        line-height: 60px;
    }
}

.SubTitle {
    font-size: 32px;
    line-height: 39px;
    //color: #FFFFFF;
