    position: relative;
    background: linear-gradient(82deg, #FFF -0.35%, rgba(255, 255, 255, 0.90) 10.14%, rgba(255, 255, 255, 0.00) 102.21%);
    color: transparent;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    z-index: 3;
